@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
:root{
  --theme-color:white;
  --heading-color:#1f2640;
  --text-color-body:#3940a5;
  --header-width:80%;
  --search-width:250px;
  --small-search-width:180px;
  --tab-selected-color:#64b305;
  --light-blue:#263f9c;
}
*{box-sizing:border-box;}
*:focus{outline:none;}
body,html{margin:0;padding:0;width:100%;height:100%;-webkit-font-smoothing: subpixel-antialiased!important;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background:var(--theme-color);
}
a{color:inherit;text-decoration:none;}
input:focus{outline:none;}
#header{width:100%;background:#1f2640;padding:12px 0 10px;box-shadow:2px 2px 6px rgba(0,0,0,0.2);}
#header a{display:block;text-decoration:none;}
#header header{position: relative;top:0;margin:0 auto;padding:0;width:var(--header-width);}
#header ul{margin:0;list-style-type:none;padding:0;width:fit-content;display:inline-block;}
#header li{margin:0;display:inline-block;height:100%;vertical-align: middle;}
#header li > em{margin:0 0 0;display:block;padding-left:10px;color:#333;margin-bottom:5px;}
#header ul.right{float:right;}


.adjust{width:var(--header-width);margin:auto;color:var(--text-color-body);}

.heading{width:fit-content;margin:20px 15px 0;font-size:32px;font-weight:700;color:var(--heading-color);}

.flex{display:flex;flex-wrap:wrap;justify-content:left;padding:0;margin:0;}
.flex > *{display:block;}

.tabs-section{padding:20px 0;}
.tab-links{background:none;padding:10px 14px;border:none;border-bottom:3px solid var(--theme-color);color:var(--text-color-body);font-size:15px;font-weight:500;}
.tab-links.active{color:var(--tab-selected-color);border-color:var(--tab-selected-color);}

.tab-content{width:100%;display:none;}
.tab-content.active{display:table;}
table.tab-content{margin:20px 0;box-shadow:0 0 5px #00000045;padding:0;border-collapse:collapse;font-size:90%;border-radius:3px;overflow:hidden;}
table *{border:none;vertical-align: center;}
td{padding:15px 15px;}
thead{background:#e2e2e2;text-transform: uppercase;font-weight:500;font-size:14px;}
.tab-content button{background:none;padding:0 4px;color:var(--light-blue);}
.tab-content .actions .flex{display:flex;flex-wrap:nowrap;justify-content:center;width:100%;}
.tab-content .actions .flex > *{width:30%;text-align:left;}
.tab-content .actions .flex > button:last-child{width:40%;}
.tab-content button,.tab-links{cursor:pointer;}
.tab-content button i{padding-right:8px;font-size:120%;}
.tab-content button.pricing i{background:gold;color:white;padding:4px 6px;margin-right:5px;border-radius:50%;font-size:80%;}
.tab-content button.csv i{color:var(--tab-selected-color)}
.tab-content button.report i{color:orangered}
.tab-content button.re-schedule i{color:var(--light-blue)}
.game-date{font-size:16px;} .game-duration{font-size:12px;opacity:0.67}

button{font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;}

.game-image{width:40px;height:40px;display:inline-block;}
.game-about{display:inline-block;padding-left:15px;vertical-align:top;}
.game-name{font-size:15px;max-width:150px;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;}
.game-region{font-size:12px;margin:5px 0 0;opacity:0.67;font-style:italic;}

#footer{background:#f4f4f4;color:#666666;padding:10px;}
#footer footer{width:var(--header-width);margin:auto;position:relative;display:flex;align-items:center;}
.footer-left{display:flex;height:100%;flex-flow:wrap;flex-wrap:wrap;align-items:center;width:70%;}
.footer-right{width:30%;height:fit-content;}
.image-link > img{height:24px;width:auto;margin:auto;}
.footer-box{height:100%;min-width:120px;display:block;text-align:center;font-size:75%;font-weight:700;background:white;border-radius:6px;max-width: 150px;word-break: break-word;white-space: normal;padding:8px;margin:15px 8px;}
.footer-right p{text-align:right;margin:8px;font-size:80%;}
.footer-link-wrapper{position: relative;text-align:right;padding:5px 0;}
.footer-link{font-weight:700;}

@media only screen and (max-width:900px){
  .tab-content .actions .flex > *{width:33% !important;}
  .medium-hide{display:none;}
}

@media only screen and (max-width:1192px){
  .tab-content .actions .flex > *{width:30%;}
  .tab-content .actions .flex > button:first-child{width:20%;}
  .tab-content .actions .flex > button:last-child{width:50%;}
  table *{font-size:95%;}
  .game-date{font-size:13px;}
  .game-duration,.game-region{font-size:10px;}
  .game-name{font-size:13px;width:90px}
  td{padding:8px 10px;}
}

@media only screen and (max-width:625px){
  .small-hide{display:none;}
  .game-about{padding:0 0 0 8px !important;}
  .heading{margin-left:0.5%;}
  header{width:95% !important;margin-left:5% !important;}
  html,body{width:100%;}
  .adjust{width:95%;}
}

@media only screen and (max-width:500px){
  .game-name{width:70px;}
  .game-image{width:30px;height:30px;}
  section{width:100%;}
  .footer-left,.footer-right{width:100%;}
  .footer-left{justify-content: center;}
  .footer-right p{text-align: center;}
}

.modal,.price-modal{position:fixed;top:0;left:0;height:100%;width:100%;background:#00000045;display:none;pointer-events: unset;}
.modal-content,.price-modal-content{position:fixed;top:50%;left:50%;transform:translate(-50%,-50%);border-radius:8px;overflow:hidden;box-shadow:0 0 8px #00000045;}
.modal-content > div:first-child{padding:10px;width:100%;background:var(--heading-color);color:white;text-align:center;}
.modal-content .select-date{padding:20px;background:white;}
input,button{cursor:pointer}
.modal-content .set-button,.modal-content .set-button button{width:100%;background:var(--light-blue);border:none;color:white;padding:5px 0;}
.modal-content .set-button:hover{opacity:0.85;}

.show-modal{display:block;pointer-events: stroke;}

.price-modal-content{background:white;padding:20px;}
.price-game-logo{width:80px;height:80px;display:inline-block;}
.money{font-weight: 700;}
.price-modal-content > .flex{position:relative;}
.price-game-about,.price-game-about > *{width:fit-content;}
.price-game-about{position:absolute;bottom:0;left:90px;} .price-modal-content > .game-name{font-size:15px !important;}

@media only screen and (max-width:400px){
  .game-date{font-size:9px;}
  .game-duration{font-size:7px;}
  td{padding:10px 5px;}
}